body{
    margin: 0;
    padding: 0;
    font-family: 'Inter', sans-serif;
}
.header{
    display: flex;
    justify-content: space-between;
    padding: 0 3rem;
    background-color: #212121;
    color: white;
}
.nav-items > ul{
    list-style-type: none;
    display: flex;
}
.nav-items >ul >li{
    padding: 1rem;
    font-family: 'Roboto', sans-serif;
}
.logo{
    width: 5rem;
}
.card{
    width: 18rem;
    border: 1px solid black;
    padding: 1rem;
    margin: 1.4rem;
}
.card > img{
    width: 100%;
}
.restaurant-list{
    display: flex;
    flex-wrap: wrap;
}






